import { Component, h, Element, State, Host, Fragment } from '@stencil/core';
import { ICellRendererComp } from 'ag-grid-community';
import type { IconTextCellParams, ITooltipCell } from '../../../../types';

export interface IconConfig {
    iconName: string;
    iconClass?: string;
    tooltip?: string;
}

export interface TextIconCellData {
    text?: string;
    icons: IconConfig[];
}

export interface ITextIconCellParams extends IconTextCellParams, ITooltipCell {
    text?: (data: any) => string;
    iconPosition?: 'before' | 'after';
    icons: () => IconConfig[];
}

type InitParams = ITextIconCellParams;

/**
 * @category Data Tables
 * @description A cell component for displaying a text with icons, Text is optional and multiple icons are supported.
 */

@Component({
    tag: 'at-text-icon-cell',
    shadow: false,
})
export class AtTextIconCell implements ICellRendererComp {
    @Element() el: HTMLElement;
    @State() text: string = '';

    private params: InitParams;

    private update(params: InitParams) {
        this.params = params;
        if (params.text) {
            this.text = params.text(params.data);
        } else {
            this.text = params.value?.toString() || '';
        }
    }

    init(params: InitParams): void {
        this.update(params);
    }

    getGui(): HTMLElement {
        return this.el;
    }

    refresh(params: InitParams): boolean {
        this.update(params);
        return true;
    }

    private renderIcons() {
        return (
            <Fragment>
                {this.params.icons().map((icon) => (
                    <at-tooltip
                        position="right"
                        disabled={!icon.tooltip}
                    >
                        <div slot="tooltip-trigger">
                            <i
                                class={`material-icons text-icon-md mt-4 cursor-pointer ${icon.iconClass || ''}`}
                            >
                                {icon.iconName}
                            </i>
                        </div>
                        {icon.tooltip && (
                            <span class="leading-normal">{icon.tooltip}</span>
                        )}
                    </at-tooltip>
                ))}
            </Fragment>
        );
    }

    render() {
        const iconPosition = this.params?.iconPosition || 'before';

        return (
            <Host class="flex h-full items-center gap-4 overflow-hidden leading-normal">
                {iconPosition === 'before' && this.renderIcons()}
                <span class="truncate">{this.text}</span>
                {iconPosition === 'after' && this.renderIcons()}
            </Host>
        );
    }
}
